@import'_ui';
//variables to complete
$tnormalcolor:black;
//base  for all the site 
canvas{
	display: block;
	z-index:0;
}

#container{
	position: absolute;
	z-index: 1000;
}

main{
	background: black;
	width: 80vh;
	height: 35vh;
	padding: 5px;
	margin: 5px;
	text-align: justify;
	color:white;
	border-color:white;
	border-style:outset;
	border-width: 0.1em;
	border-left-style: none;
	border-right-style: none;
	font-size:$mainSize;
	word-wrap:break-all;
	overflow-wrap: break-word;
	overflow-y:scroll ;
	opacity: 0.7;
	
}
#portafolio{ 
	 width: 120vh;
	 height: 80vh;
	 margin-bottom: 2.5em;
	 font-size:1.5rem;
	 overflow-x:scroll;
	 display: grid;
	 column-gap: 2px;
	 row-gap: 40px;
	 grid-template-columns: 1fr 1fr;
	 overflow-x: hidden;
	
}

.proj {
  width:50vh;
  font-size: 1rem;
  
}

.Sites{
	background: black;
	width: 96vh;
	height: 30vh;
	padding: 5px;
	margin: 5px;
	color:white;
	border-color:white;
	border-style:outset;
	border-width: 0.1em;
	border-left-style: none;
	border-right-style: none;
	font-size:$mainSize;
	overflow-y:scroll ;
	opacity: 0.7;
	
	a{
		color: white;
		font-weight: bold;
		border-bottom:1px solid white;
	}
	a:hover{
		color: black;
	}
}

ul{
	list-style-type: none;
	text-align: center;
	padding: 10px;
}
li{
	margin:15px;

}

li:hover{
	background:white;
	color:black
}

li::before{

  position: absolute;
  left    : 0;
  bottom  : 0;
  height  : 1px;
  width   : 10%;  /* or 100px */
  border-bottom:1px solid white;

}
#ui{
	z-index: 1000;
	bottom: 10vh;
	position: absolute;
}

//footer and quote

#contact {
	z-index: 1000;
	position: absolute;
	text-align: center;
	bottom: 0vh;
	margin-right: 4vh;
	

}

small{
   background-color: black;
   color:white;
   padding: 3px;
}


//responsive------------------------------------------------------------
@media screen and(max-width:600px){
	main{
		width: 44vh;
		height: 42vh;
		font-size: 1rem;
	}
	
	#ui{	
		top: 2vh;
		width: auto;
		height: 100px;
	}

	#portafolio{
		background-color: black;
		width: 52vh;
		height: 52vh;
		font-size: 2rem;
		opacity: 0.7;
		text-align: center;
		overflow-y: scroll;
		border-color:$borderColor;
		border-style:outset;
		border-width: 0.1em;
		border-left-style: none;
		border-right-style: none;
		
			ul{
				list-style-type: none;
				text-align: center;
			}
			a{color: white;}
	}

	button {
		padding: 5px;
		font-size: 1rem;
	}


	.Sites{
		width: 50vh;
		height: 50vh;
		a{
			line-height: 25px;
		}
	}
}
